import styled from 'styled-components';

export const GroundStyled = styled.div`
    position: fixed;
    width: 100vw;
    height: 25vh;
    bottom: 0px;
    border-top: 10px solid black;
    background-image: radial-gradient(ellipse at top, #2FEC86 -100%, #fff 100%);
    z-index: 5;
`;


